import ChatContainer from '../components/ChatContainer.vue'
import YfDrag from 'components/YfDrag.vue'
import DragResize from 'components/DragResize.vue'
import isEqual from 'lodash.isequal'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations, mapActions } = createNamespacedHelpers('Chat')
export default {
	name: 'Dialog',
	components: { ChatContainer, DragResize, YfDrag },
	data() {
		return {
			timerGlobal: null,
			dragging: false,
			dragStopped: true,
			windowConfig: CONFIG,
			addingRoom: false,
			dialogLoading: false,
			dialogModel: false,
			dragTimeout: 300
		}
	},
	computed: {
		...mapGetters(['miniMode', 'data', 'config', 'getDetailPreview']),
		dialog: {
			get() {
				return this.$store.getters['Chat/dialog']
			},
			set(isOpen) {
				this.setDialog(isOpen)
			}
		},
		coordinates: {
			get() {
				return this.$store.getters['Chat/coordinates']
			},
			set(coords) {
				this.setDragState()
				this.setCoordinates(coords)
			}
		},
		buttonCoordinates: {
			get() {
				return this.$store.getters['Chat/buttonCoordinates']
			},
			set(coords) {
				if (
					!isEqual(
						{ left: coords.left, top: coords.top },
						{ ...this.$store.getters['Chat/buttonCoordinates'] }
					)
				) {
					this.setDragState()
					this.setButtonCoordinates(coords)
				}
			}
		},
		computedMiniMode() {
			return this.$q.platform.is.desktop ? this.miniMode : false
		},
		buttonAnimationClasses() {
			return this.data.amountOfNewMessages ? 'animated shake' : ''
		},
		hasCurrentRecordChat() {
			let id = false
			if (this.isDetail) {
				id = app.getRecordId()
			}
			if (this.getDetailPreview && this.config.chatModules.some(el => el.id === this.getDetailPreview.module)) {
				id = this.getDetailPreview.id
			}
			if (id && !this.data.roomList.crm[id]) {
				return true
			} else {
				return false
			}
		},
		isDetail() {
			return (
				this.windowConfig.view === 'Detail' &&
				this.config.chatModules.some(el => el.id === this.windowConfig.module)
			)
		},
		dialogClasses() {
			return {
				'quasar-reset': true,
				animated: true,
				slideOutDown: !this.dialog,
				slideInUp: this.dialog,
				'all-pointer-events': !this.dragStopped
			}
		}
	},
	created() {
		this.startUpdatesListener()
	},
	updated() {
		this.initDialogModel()
	},
	methods: {
		...mapMutations(['setDialog', 'setCoordinates', 'setButtonCoordinates', 'updateRooms', 'setTab']),
		...mapActions(['startUpdatesListener', 'fetchRoom']),
		initDialogModel() {
			if (!this.dialogModel && this.dialog) {
				this.dialogModel = true
			}
		},
		showDialog() {
			this.dragging = false
			setTimeout(_ => {
				if (!this.dragging && !this.addingRoom) {
					this.dialog = !this.dialog
				}
			}, this.dragTimeout)
		},
		addRecordRoomToChat() {
			setTimeout(_ => {
				this.addingRoom = true
				AppConnector.request({
					module: 'Chat',
					action: 'Room',
					mode: 'addToFavorites',
					roomType: 'crm',
					recordId: this.isDetail ? app.getRecordId() : this.getDetailPreview.id
				}).done(({ result }) => {
					this.addingRoom = false
					this.updateRooms(result)
					this.$q.notify({
						position: 'top',
						color: 'success',
						message: this.translate('JS_CHAT_ROOM_ADDED'),
						icon: 'mdi-check'
					})
					this.fetchRoom({ id: result.record_id, roomType: 'crm' }, false).then(_ => {
						this.dialog = true
						this.setTab('chat')
					})
				})
			}, this.dragTimeout)
		},
		setDragState() {
			this.dragging = true
			this.dragStopped = false
		},
		onDragstop(e) {
			this.dragStopped = true
		}
	}
}
